// key
export  const getArrayValByKey = (arr,key) =>{
    if(!arr) return ''
    let str = ''
    if(!key) key = 'name'
    arr.forEach(element => {
        str += element[key] + ' '
    });
    return str
}

export const getnewDate = (data) =>{  //
   let m = new Date((data)*1000)
   let b = m.toLocaleDateString()+''
   return  b.replace(/\//g,'-')
}

export const changeline=(data)=>{   //
    return data.replace(/\|/g,' | ')
    // console.log(data)
}
export const getnewDay = (data) =>{  //
    let m = new Date((data)*1000)
    let n = (m.getDay())
    let arr=['','','','','','','',]
    let D = arr[n]  // 
    let y = m.getMonth()+1
    let r = m.getDate()
    return ''+D+' '+y+''+r+''
  
 }

 export const getName =(data)=>{
    //  console.log(data)
     let str=""
     data.forEach(item=>{
        str+=item.name
     })
     return str
 }

//xxxx
export const getDateTime=(data)=>{
    data =new Date(data*1000)
    let h=data.getHours()
    let m = data.getMinutes()
    if(m<10)m=m+'0'
    return h+':'+m
}

    // xxxx
export const getday=(data)=>{
    let m = new Date(data*1000)
        return ''+''.charAt(m.getDay())+(m.getMonth()+1)+''+m.getDate()+''
   
}

export const loginuser=(data)=>{
    if(!localStorage.getItem('username')||localStorage.getItem('username')==''){
        return data
    }else{
        return localStorage.getItem('username')
    }
}

