<template>
  <div class="film" v-if="info">
    <el-sticky :info="info"></el-sticky>
    <div class="lazy1-img">
      <div class="bgimg">
        <img
          src="data:image/png;base64,"
          alt
        />
      </div>
      <div class="banner">
        <img :src="info.poster" />
      </div>
    </div>
    <div class="film-detail">
      <div>
        <span class="name">{{info.name}}</span>
        <span class="type">{{info.filmType.name}}</span>
      </div>
      <div>
        <span>{{info.category|changeline}}</span>
      </div>
      <div>
        <span>{{info.premiereAt|getnewDate}}</span>
      </div>
      <div>
        <span>{{info.nation}}&ensp;|&ensp;{{info.runtime}}</span>
      </div>
      <div
        ref="box"
        class="film-synopsis"
        :style="{height:height}"
        :class="flag?'min':''"
      >{{info.synopsis}}</div>
      <div class="toggle">
        <img
          src="@/assets/img/down-arrow.png"
          @click="change"
          :style="{transform:'rotate('+trans+'deg'+')'}"
          alt
        />
      </div>
    </div>
    <!---->
    <div class="actor">
      <div class="actor-title-bar">
        <span class="actor-title-text"></span>
      </div>
      <em-swiper :info="info"></em-swiper>
    </div>

    <!---->
    <div class="photo">
      <div class="photos-title-bar">
        <span class="photos-title-text"></span>
        <span class="photos-to-all" @click="changehide1" v-if="Length">({{info.photos.length}})></span>
      </div>
      <em-juzhao :info="info" v-if="Length" v-on:changego="changego1"></em-juzhao>
      <div v-if="!Length" class="moviejuzhao"></div>
    </div>
    <div class="goschedule"></div>

    <!-- -->
    <div class="film-header1" v-show="hide">
      <img
        @click="changehide"
        class="goBack1"
        src="data:image/png;base64,"
        alt
      />
      <div class="juzhao-title" v-if="Length">({{info.photos.length}})</div>
    </div>
    <!---->
    <div class="all-photos" v-show="hide">
      <div class="juzhao-img">
        <div v-for="(item,index) in info.photos" :key="index" class="juzhao-img-de">
          <a class="smallimg" @click="changehidde(index)">
            <img :src="item" alt />
          </a>
        </div>
      </div>
    </div>

    <!---->
    <div class="big-zoom-img" v-if="hidde">
      <!---->
      <div class="header-photo">
        <img class="goback2" src="@/assets/img/X.png" @click="goback3" />
        <div class="header-photo-title" v-if="Length">{{index+1}}/{{info.photos.length}}</div>
      </div>
      <div class="swiper-container header-img">
        <div class="swiper-wrapper header-img-box">
          <div class="swiper-slide" v-for="(item,index) in info.photos" :key="index">
            <img :src="item" alt />
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { getFilmDetail } from "@/api/film";
import { changeline } from "@/api/film";
import ElSticky from "./Filmdetailsticky";
import Swiper from "swiper";
import EmSwiper from "./Emswiper";
import EmJuzhao from "./EmJuzhao";
export default {
  components: {
    ElSticky,
    EmSwiper,
    EmJuzhao
  },
  data() {
    return {
      hide: false,
      info: "",
      Length: "",
      trans: "0",
      height: "",
      flag: false,
      add: false,
      index: 0,
      hidde: false
    };
  },
  created() {
    this.getDetail();
  },
  mounted() {},
  updated() {
    this.init();
    if (this.add == false) {
      this.flag = !this.flag;
      this.height = this.$refs.box.offsetHeight + "px";
      this.add = true;
    }
    // console.log(this.$refs.box)
  },

  methods: {
    changego1(data) {
      console.log(data, "----");
      this.index = data;
      this.hidde = true;
      console.log(this.index);
    },
    init() {
      var _this = this;
      var mySwiper1 = new Swiper(".header-img",{
        preventInteractionOnTransition : true,
        initialSlide:_this.index,
        on: {
          slideChangeTransitionEnd(){
            _this.index = this.activeIndex; //slide
          // console.log(_this.index)
         }
        }
      });
    },
    getDetail() {
      getFilmDetail(this.$route.params.id).then(result => { 
        let temp = result.data.film;
        this.info = temp;
        this.Length = temp.photos ? temp.photos.length : false;
      });
    },
    change() {
      this.flag = !this.flag;
      if (this.trans == "180") {
        this.trans = "0";
      } else {
        this.trans = "180";
      }
    },
    changehide() {
      this.hide = false;
    },
    changehide1() {
      this.hide = true;
    },
    addindex(data) {
      this.index = data;
      this.init(data);
    },
    changehidde(data) {
      this.hidde = true;
      this.index = data;
    },
    goback3() {
      this.hidde = false;
    }
  }
};
</script>
<style scoped>

.min {
  height: 0.76rem !important;
}
.name {
  color: #191a1b;
  font-size: 18px;
  height: 24px;
  line-height: 24px;
  margin-right: 7px;
}
.lazy1-img {
  width: 100%;
  background: rgb(249, 249, 249);
  position: relative;
  height: 56vw;
  overflow: hidden;
}
.bgimg {
  height: 56vw;
  display: flex;
  -webkit-box-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
}
.banner img {
  width: 100%;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}
.type {
  font-size: 9px;
  color: #fff;
  background-color: #d2d6dc;
  height: 14px;
  line-height: 14px;
  padding: 0 2px;
  border-radius: 2px;
  display: inline-block;
}
.banner {
  opacity: 1;
  display: block;
  position: absolute;
  overflow: hidden;
  top: 0;
  width: 100%;
  height: 56vw;
  overflow: hidden;
  top: 0;
  height: 56vw;
}
.film-detail {
  padding: 15px;
  padding-top: 12px;
  background-color: #fff;
  text-align: left;
}
.film-detail div {
  font-size: 13px;
  color: #797d82;
  margin-top: 4px;
  line-height: 1.5;
}
.film-synopsis {
  transition: height 0.5s;
  overflow: hidden;
  line-height: 1.5;
  margin-top: 12px;
}
.toggle {
  text-align: center;
  display: block;
  height: auto;
  width: 20px;
  margin: auto;
  line-height: normal;
}
.toggle img {
  width: 8px;
  margin: auto;
}
.actor {
  margin-top: 10px;
  background-color: #fff;
}
.actor-title-bar {
  width: 100%;
  padding: 15px;
  font-size: 16px;
  line-height: 1.5;
}
.photo {
  margin-top: 10px;
  margin-bottom: 60px;
  background-color: #fff;
}
.photos-title-bar {
  width: 100%;
  padding: 15px;
  font-size: 16px;
  line-height: 1.5;
}
.photos-title-text {
  font-size: 16px;
  text-align: left;
  color: #191a1b;
  padding: 0;
  display: inline-block;
  height: 22.5px;
  line-height: 22px;
}
.photos-to-all {
  float: right;
  font-size: 13px;
  color: #797d82;
  height: 22.5px;
  line-height: 22px;
}
.goschedule {
  position: fixed;
  bottom: 0;
  left: 0;
  height: 49px;
  width: 100%;
  text-align: center;
  background-color: #ff5f16;
  color: #fff;
  font-size: 16px;
  line-height: 49px;
  z-index: 5;
}
.film-header1 {
  width: 100vw;
  height: 44px;
  z-index: 15;
  background-color: #fff;
  color: #191a1b;
  position: fixed;
  top: 0;
  z-index: 99;
}
.goBack1 {
  height: 30px;
  position: absolute;
  top: 5px;
  left: 5px;
}
.juzhao-title {
  font-size: 17px;
  line-height: 44px;
  width: 100vw;
  text-align: center;
}
.all-photos {
  position: fixed;
  top: 44px;
  left: 0;
  right: 0;
  bottom: 0;
  overflow-y: auto;
  background-color: #fff;
  z-index: 100;
}
.juzhao-img {
  flex-wrap: wrap;
  display: -webkit-flex;
  justify-content: flex-start;
  -webkit-box-pack: start;
  align-content: center;
}
.juzhao-img-de {
  width: calc((100vw - 3px) / 3);
  height: calc((100vw - 3px) / 3);
  margin-right: 1px;
  display: inline-block;
  margin-bottom: 1.5px;
}
.smallimg {
  width: 124px;
  height: 124px;
  display: inline-block;
}
.smallimg img {
  width: 100%;
  height: 100%;
}
.header-photo {
  position: fixed;
  background-color: #000;
  color: transparent;
  transition: all 0.3s ease;
  width: 100vw;
  height: 44px;
  z-index: 101;
  top: 0;
}
.header-photo img {
  width: 20px;
  height: 20px;
}
.goback2 {
  height: 30px;
  position: absolute;
  top: 10px;
  left: 10px;
  width: 22px;
  height: 22px;
  color: #fff;
}
.big-zoom-img {
  width: 100%;
  height: 100%;
  z-index: 3001;
  position: fixed;
  background-color: #000;
  top: 0;
}
.header-photo-title {
  font-size: 17px;
  line-height: 44px;
  width: 100vw;
  text-align: center;
  color: #fff;
}
.header-img {
  z-index: 100;
  overflow: hidden;
  position: relative;
  height: 100%;
}

.header-img-box {
  /* overflow: hidden; */
  position: relative;
  height: 100%;
}
.header-img-box div {
  width: 100%;
  height: 100%;
  align-items: center;
  display: -webkit-inline-flex !important;
}
.header-img-box img {
  width: 100%;
}
.moviejuzhao {
  height: 115px;
  line-height: 58px;
  font-size: 14px;
  color: #bdc0c5;
  margin: auto;
  text-align: center;
}
</style>