import axios from "axios";
// axios,ajax
const instance = axios.create({
  baseURL: 'https://m.maizuo.com',
  timeout: 1000
});
// axios? api
// axios
instance.interceptors.request.use(function (config) {
    // Do something before request is sent
    // console.log('--------------',config.headers['X-Host'])
    // 
    // console.log(config.data)
    let xhost = config.data.xhost
    config.headers['X-Host'] = xhost
    delete config.data.xhost
    config.headers['X-Client-Info'] = '{"a":"3000","ch":"1002","v":"5.0.4","e":"156767148210033043604248"}'
    // getversion2.0
    // console.log(config.url) // api

    // url
    config.url += (config.url.indexOf('?') === -1 ? '?' : '&')+'version=2.0'
    return config;
  }, function (error) {
    // Do something with request error
    return Promise.reject(error);
});

// (then)
instance.interceptors.response.use(function(response){
    // console.log('...',response)
    // if(response.data.data.code != 0){
    //     alert('!')
    // }
    return Promise.resolve(response.data)
},function(error){
    // console.log('...',error)
})

export default instance